@import url('https://fonts.googleapis.com/css2?family=Great+Vibes&family=Inter+Tight:ital,wght@1,100&display=swap');

* {
    margin: 0;
    padding: 0;
}
.stars, .twinkling {
    position:absolute;
    top:0;
    left:0;
    right:0;
    bottom:0;
    width:100%;
    height:100%;
    display:block;
}
.stars {
    background: #000 url(../Images/stars.png) repeat top center;
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center;
    z-index: 0;
}

.twinkling {
    background: transparent url(../Images/twinkling.png) repeat top center;
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center;
    z-index: 1;
    animation: move-twinkle-back 200s linear infinite;
}

.Picture {
    position: absolute;
    top: 250px;
    left: 500px;
    overflow: hidden;
    justify-content: left;
    border-radius: 50%;
    margin: auto;
    overflow-x: auto;
    overflow-y: hidden;
    z-index: 3;
    background-color: #fff;
    box-shadow:
      0 0 15px 7px #fff,  /* inner white */
      0 0 25px 15px #f0f, /* middle magenta */
      0 0 35px 22px #0ff; /* outer cyan */
    cursor: pointer; 
    -webkit-transition-property: all; 
    -webkit-transition-duration: 0.3s; 
    -webkit-transition-timing-function: ease; 
  }

.Picture:hover { 
    transform: scale(1.2); 
    }

.texts {
    position: absolute;
    top: 230px;
    left: 800px;
    float: right;
}
.Name {
    position: absolute;
    top: 80px;
    color: #f0f;
    font-family: 'Great Vibes', cursive;
    font-size:50px;
    background: -webkit-linear-gradient(#eee, #333);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-shadow: 6px 6px 0px rgba(94, 81, 81, 0.2);
    display:block;
    position: relative;
    z-index: 3;

}

.Title {
    position: absolute;
    top: 180px;
    font-family:'Inter Tight';
    color: white;
    font-weight: lighter;
    font-size:40px;
    background: -webkit-linear-gradient(#eee, #333);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-shadow: 6px 6px 0px rgba(114, 105, 105, 0.2);
    display:block;
    z-index: 3;


}

.github {
    position: absolute;
    max-width: 100%;
    height: auto;
    top: 450px;
    left: 150px;
    z-index: 3;
    border-radius: 50%;
    box-shadow:
      0 0 7px 3px #000000,  /* inner white */
      0 0 12px 7px rgb(4, 255, 25), /* middle magenta */
      0 0 17px 11px #0ff; /* outer cyan */
    cursor: pointer; 
    -webkit-transition-property: all; 
    -webkit-transition-duration: 0.3s; 
    -webkit-transition-timing-function: ease; 
    }

.github:hover { 
    transform: scale(1.2); 
}

.linkedin {
    position: absolute;
    top: 100px;
    left: 1500px;
    z-index: 3;
    overflow: hidden;
    justify-content: left;
    border-radius: 50%;
    background-color: #1885c0;
    box-shadow:
    0 0 7px 3px #000000,  /* inner white */
    0 0 12px 7px rgb(66, 201, 255), /* middle magenta */
    0 0 17px 11px #0ff; /* outer cyan */
    cursor: pointer; 
    -webkit-transition-property: all; 
    -webkit-transition-duration: 0.3s; 
    -webkit-transition-timing-function: ease; 
    }

.linkedin:hover { 
    transform: scale(1.2); 
}

.ig {
    position: absolute;
    top: 650px;
    left: 1000px;
    z-index: 3;
    border-radius: 50%;
    box-shadow:
    0 0 7px 2px #000000,  /* inner white */
    0 0 12px 5px rgb(66, 201, 255), /* middle magenta */
    0 0 15px 12px rgb(255, 6, 255); /* outer cyan */
    cursor: pointer; 
    -webkit-transition-property: all; 
    -webkit-transition-duration: 0.3s; 
    -webkit-transition-timing-function: ease; 
    }

.ig:hover { 
    transform: scale(1.2); 
}

.outlook {
    position: absolute;
    top: 750px;
    left: 1500px;
    border-radius: 50%;
    z-index: 3;
    box-shadow:
    0 0 7px 3px rgb(66, 201, 255),  /* inner white */
    0 0 12px 7px rgb(66, 201, 255), /* middle magenta */
    0 0 17px 11px #0ff; /* outer cyan */
    cursor: pointer; 
    -webkit-transition-property: all; 
    -webkit-transition-duration: 0.3s; 
    -webkit-transition-timing-function: ease; 
    }

.outlook:hover { 
